import { Button } from "@/components/ui/button"
import { PhoneCall } from "lucide-react"

// This is the main parent component for the Emergency page.
export default function EmergencyPage() {
  return (
    <>
      <EmergencyHero />
    </>
  )
}

// EmergencyHero component - A hero section for the emergency services page.
function EmergencyHero() {
  return (
    <section className="relative bg-red-800 py-20 text-white dark:bg-gray-950">
      {/* Background decorative elements. Using Tailwind's dark:bg-gray-950 for the dark mode background. The red theme will be preserved on light mode, and dark mode will use the dark gray theme. */}
      <div className="absolute inset-0 bg-gray-950 dark:bg-gray-950 opacity-20 mix-blend-overlay"></div>
      <div className="container mx-auto px-4">
        <div className="grid gap-8 md:grid-cols-2 md:gap-12">
          <div className="flex flex-col justify-center">
            {/* The emergency badge is white with red text in light mode, and solid red with white text in dark mode. */}
            <div className="mb-4 inline-block rounded-full bg-white px-4 py-1 text-sm font-semibold text-red-600 dark:bg-red-600 dark:text-white">
              24/7 EMERGENCY SERVICES
            </div>
            <h1 className="mb-6 text-4xl font-bold leading-tight text-white md:text-5xl lg:text-6xl">
              Immediate Medical Response When Every Second Counts
            </h1>
            <p className="mb-8 text-lg text-white md:text-xl dark:text-gray-300">
              Our emergency medical team is available 24/7 across Rwanda to provide rapid response and life-saving care
              during critical situations.
            </p>
            <div className="flex flex-col space-y-4 sm:flex-row sm:space-x-4 sm:space-y-0">
              {/* The main call-to-action button */}
              <Button size="lg" className="bg-white text-red-600 hover:bg-gray-100 dark:bg-red-600 dark:text-white dark:hover:bg-red-700">
                <PhoneCall className="mr-2 h-5 w-5" />
                Call Emergency: 912
              </Button>
              {/* The secondary button */}
              <Button size="lg" variant="outline" className="border-white text-white hover:bg-white hover:text-red-600 dark:border-red-600 dark:text-red-600 dark:hover:bg-red-600 dark:hover:text-white">
                Learn More
              </Button>
            </div>
          </div>
          <div className="flex items-center justify-center">
            <div className="relative h-64 w-full overflow-hidden rounded-lg md:h-96">
              <div className="absolute inset-0 flex items-center justify-center bg-red-500/10">
                <div className="grid grid-cols-2 gap-4 p-6 text-center">
                  {/* The stats boxes use a semi-transparent background that works for both modes. */}
                  <div className="rounded-lg bg-white/10 p-4 backdrop-blur-sm">
                    <p className="text-3xl font-bold text-white md:text-4xl">5-15</p>
                    <p className="text-sm font-medium text-white/80">Minute Response Time</p>
                  </div>
                  <div className="rounded-lg bg-white/10 p-4 backdrop-blur-sm">
                    <p className="text-3xl font-bold text-white md:text-4xl">24/7</p>
                    <p className="text-sm font-medium text-white/80">Service Availability</p>
                  </div>
                  <div className="rounded-lg bg-white/10 p-4 backdrop-blur-sm">
                    <p className="text-3xl font-bold text-white md:text-4xl">30+</p>
                    <p className="text-sm font-medium text-white/80">Emergency Vehicles</p>
                  </div>
                  <div className="rounded-lg bg-white/10 p-4 backdrop-blur-sm">
                    <p className="text-3xl font-bold text-white md:text-4xl">100%</p>
                    <p className="text-sm font-medium text-white/80">Trained Responders</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
